.single{
    display: flex;
    width: 100%;

    .singleContainer{
        flex:6;

        .top{
            padding: 20px;
            display: flex;
            gap:20px;

            .left{

                flex:1;
                -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
                box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
                padding: 20px;
                position:relative;

               

                .editButton{
                    position:absolute;
                    top:0px;
                    right:0px;
                    padding: 5px;
                    font-size: 12px;
                    color:#7451f8;
                    background-color: #7551f81c;
                    border-radius:0px 0px 0px 5px;
                    font-weight: 500;
                }


                .item{

                    display: flex;
                    gap: 20px;

                    .itemImg{
                        width: 100px;
                        height: 100px;
                        object-fit:cover;
                        border-radius: 50%;
                    }

                    .details{
                        .itemTitle{
                            margin-bottom: 10px;
                            color:#555;
                        }

                        .detailItem{
                            margin-bottom: 10px;
                            font-size: 14px;

                            .itemKey{
                                font-weight: bold;
                                color:gray;
                                margin-right: 10px;
                            }

                            .itemValue{
                                font-weight: 300;
                            }
                        }
                    }
                }
            }

            .right{
                flex:2;
                -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
                box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
                padding: 20px;
            }
        }
        .title{
            font-size: 16px;
            color:lightgray;   
            margin-bottom:10px 20px;      
     }
 
 
     .bottom{
         -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
         box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
         padding: 20px;
         margin: 20px;
     }
    }


}